.dialogNamesWrapper {
    
}

.dialogNames {
    padding-left: 10px;
    border-left: 1px solid grey;
}

.dialog {
    margin: 5px 0px;
}

.dialog a {
    display: flex;
    align-items: center;
    text-decoration: none;
    font-size: 18px;
    color: rgb(255, 255, 255);
    
}

.dialog a img {
    height: 45px;
    border: 1px solid royalblue;
    border-radius: 35%;
    background-color: salmon;
    margin-right: 5px;
}

.dialog a.active {
    color: rgba(251, 255, 20, 0.952);
}
